                    <el-col :span="12"
                      ><div class="rentPlanText">
                        <span>商铺数量</span><span class="rentPlanNum">36</span>
                      </div></el-col
                    >
                    <el-col :span="12"
                      ><div class="rentPlanText">
                        <span>应收房租</span
                        ><span class="rentPlanNum">26589元</span>
                      </div></el-col
                    >
                  </el-row>
                  <el-row :gutter="20">
                    <el-col :span="12"
                      ><div class="rentPlanText">
                        <span>未交房租商铺</span
                        ><span class="rentPlanNum">3</span>
                      </div></el-col
                    >
                    <el-col :span="12"
                      ><div class="rentPlanText">
                        <span>实收房租</span
                        ><span class="rentPlanNum">20080元</span>
                      </div></el-col
                    >
                  </el-row>
                </div>
              </div>
            </dv-border-box-1>
          </div>
        </el-col>
        <el-col :span="24">
