                  <el-row :gutter="20">
                    <el-col :span="24"
                      ><div class="rentPlanText">
                        <span>人数统计</span
                        ><span class="rentPlanNum">45856584人</span>
                      </div></el-col
                    >
                  </el-row>
                  <el-row :gutter="20">
                    <el-col :span="24"
                      ><div class="rentPlanText">
                        <span>团队支持</span
                        ><span class="rentPlanNum">56584人</span>
                      </div></el-col
                    >
                  </el-row>
                </div>
              </div>
            </dv-border-box-9>
          </div>
        </el-col>
      </el-row>
    </div>
    <div class="col">
      <el-row :gutter="20">
        <el-col :span="14"
          ><div class="grid-content bg-purple firstLine">
            <dv-border-box-6>
              <div class="box">
                <dv-loading v-if="loadding">Loading...</dv-loading>
                <div v-else>
                  <v-title text="经营分析"></v-title>
                  <div
                    id="analyseChart"
                    :style="{
                      width: '500px',
                      height: '205px',
                      marginTop: '-25px',
                    }"
                  ></div>
                </div>
              </div>
            </dv-border-box-6></div
        ></el-col>
        <el-col :span="10"
          ><div class="grid-content bg-purple firstLine">
            <dv-border-box-6>
              <div class="box">
                <dv-loading v-if="loadding">Loading...</dv-loading>
                <div v-else>
                  <v-title text="物业费"></v-title>
                  <div
                    id="propertyChart"
                    :style="{
                      width: '330px',
                      height: '190px',
                      marginTop: '-45px',
                    }"
                  ></div>
                </div>
              </div>
            </dv-border-box-6></div
        ></el-col>
      </el-row>
      <el-row :gutter="20">
        <el-col :span="14"
          ><div class="grid-content bg-purple secondLine">
            <dv-border-box-7>
              <div class="box">
                <dv-loading v-if="loadding">Loading...</dv-loading>
                <div v-else>
                  <v-title text="商铺用量"></v-title>
                  <div class="storeContent">
                    <div class="tabs">
                      <div class="active">1F</div>
                      <div>2F</div>
                      <div>3F</div>
                      <div>4F</div>
                    </div>
                    <div class="list">
                      <dv-scroll-board
                        :config="list"
                        style="width: 400px; height: 140px; margin-top: 15px"
                      />
                    </div>
                  </div>
                </div>
              </div>
            </dv-border-box-7></div
        ></el-col>
        <el-col :span="10"
          ><div class="grid-content bg-purple secondLine">
            <dv-border-box-7>
              <div class="box">
                <dv-loading v-if="loadding">Loading...</dv-loading>
                <div v-else>
                  <div
                    id="chart"
                    :style="{
                      width: '330px',
                      height: '225px',
                      marginTop: '-45px',
                    }"
                  ></div>
                </div>
              </div>
            </dv-border-box-7></div
        ></el-col>
      </el-row>
      <el-row :gutter="20">
        <el-col :span="24"
          ><div class="grid-content bg-purple thirdLine">
            <dv-border-box-2>
              <div class="box">
                <dv-loading v-if="loadding">Loading...</dv-loading>
                <div v-else>
                  <div
                    id="tendencyChart"
                    :style="{
                      width: '750px',
                      height: '205px',
                      marginTop: '-55px',
                    }"
                  ></div>
                </div>
              </div>
            </dv-border-box-2></div
        ></el-col>
      </el-row>
