<script>
import title from "../components/title.vue";
export default {
  data: () => {
    return {
      loadding: false,
      list: {
        data: [
          //商铺电费列表
          ["101 恒昌药店", "用水量15564t", "2月水费:已交"],
          ["101 恒昌药店", "用水量15564t", "2月水费:已交"],
          ["101 恒昌药店", "用水量15564t", "2月水费:已交"],
          ["101 恒昌药店", "用水量15564t", "2月水费:已交"],
          ["101 恒昌药店", "用水量15564t", "2月水费:已交"],
          ["101 恒昌药店", "用水量15564t", "2月水费:已交"],
          ["101 恒昌药店", "用水量15564t", "2月水费:已交"],
        ],
        rowNum: 4,
        oddRowBGC: "#122A56",
        evenRowBGC: "#1D4282",
      },
    };
  },
  components: {
    "v-title": title,
  },
  mounted: function () {
    // 绘制echarts
    this.drawLine();
  },
  methods: {
    paymentFormat(percentage) {
      return `${percentage}%房租缴纳`;
    },
    percentageFormat(percentage) {
      return `${percentage}%完成率`;
    },
    drawLine() {
      //经营分析柱状图
      let analyseChart = this.$echarts.init(
        document.getElementById("analyseChart")
      );
      analyseChart.setOption({
        tooltip: {
          trigger: "axis",
        },
        legend: {
          data: ["盈利", "亏损"],
          textStyle: {
            color: "#0CEBFF",
          },
        },
        xAxis: [
          {
            type: "category",
            splitLine: {
              show: false,
            },
            axisLabel: {
              color: "#fff",
            },
            data: [
              "1月",
              "2月",
              "3月",
              "4月",
              "5月",
              "6月",
              "7月",
              "8月",
              "9月",
              "10月",
              "11月",
              "12月",
            ],
          },
        ],
        yAxis: [
          {
            type: "value",
            axisLabel: {
              color: "#A596FF",
            },
            splitLine: {
              show: true,
              lineStyle: {
                color: "#81A5C3",
              },
            },
          },
        ],
        series: [
          {
            name: "盈利",
            type: "bar",
            itemStyle: {
              color: "#0AEFFF",
            },
            data: [
              2.0, 4.9, 7.0, 23.2, 25.6, 76.7, 135.6, 162.2, 32.6, 20.0, 6.4,
              3.3,
            ],
            markPoint: {
              data: [
                { type: "max", name: "最大值" },
                { type: "min", name: "最小值" },
              ],
            },
          },
          {
            name: "亏损",
            type: "bar",
            itemStyle: {
              color: "#600D96",
            },
            data: [
              2.6, 5.9, 9.0, 26.4, 28.7, 70.7, 175.6, 182.2, 48.7, 18.8, 6.0,
              2.3,
            ],
            markPoint: {
              data: [
                { name: "年最高", value: 182.2, xAxis: 7, yAxis: 183 },
                { name: "年最低", value: 2.3, xAxis: 11, yAxis: 3 },
              ],
            },
          },
        ],
      });
      //物业费面积图
      let propertyChart = this.$echarts.init(
        document.getElementById("propertyChart")
      );
      propertyChart.setOption({
        tooltip: {
          trigger: "axis",
          axisPointer: {
            type: "cross",
            label: {
              backgroundColor: "#6a7985",
            },
          },
        },
        grid: {
          left: "0%",
          bottom: "10%",
          containLabel: true,
        },
        xAxis: [
          {
            type: "category",
            boundaryGap: false,
            data: [
              "1月",
              "2月",
              "3月",
              "4月",
              "5月",
              "6月",
              "7月",
              "8月",
              "9月",
              "10月",
              "11月",
              "12月",
            ],
            axisLabel: {
              color: "#fff",
            },
          },
        ],
        yAxis: [
          {
            type: "value",
            axisLabel: {
              color: "#fff",
            },
            splitLine: {
              show: true,
              lineStyle: {
                color: "#34496F",
                type: "dashed",
              },
            },
          },
        ],
        series: [
          {
            name: "物业费",
            type: "line",
            stack: "总量",
            areaStyle: {
              color: "#52b3d9",
            },
            emphasis: {
              focus: "series",
            },
            data: [120, 132, 101, 134, 90, 230, 210, 32, 321, 544, 657, 87],
            smooth: true,
          },
        ],
      });
      //chart 未知柱状图
      let chart = this.$echarts.init(document.getElementById("chart"));
      chart.setOption({
        grid: {
          left: "0%",
          bottom: "0%",
          containLabel: true,
        },
        tooltip: {
          trigger: "axis",
          axisPointer: {
            type: "line",
            label: {
              backgroundColor: "#6a7985",
            },
          },
          formatter: "{b}<br />{c}t",
        },
        xAxis: {
          type: "category",
          data: ["1F", "2F", "3F", "4F"],
          splitLine: {
            show: true,
            lineStyle: {
            color: 'rgba(98, 217, 255, .3)'
            },
          },
          axisLabel: {
            color: "#fff",
          },
        },
        yAxis: {
          type: "value",
          axisLabel: {
            color: "#fff",
          },
          splitLine: {
            show: true,
            lineStyle: {
             color: 'rgba(98, 217, 255, .3)'
            },
          },
        },
        series: [
          {
            data: [120, 200, 150, 80],
            type: "bar",
            itemStyle: {
              color: "#3EC1EF",
            },
            label: {
              show: true,
              position: "top",
              color: "#fff",
              formatter: "{c}w",
            },
          },
        ],
      });
      //房租趋势折线图 tendencyChart
      let tendencyChart = this.$echarts.init(
        document.getElementById("tendencyChart")
      );
      tendencyChart.setOption({
        tooltip: {
          trigger: "axis",
        },
        grid: {
          right: "-20%",
          bottom: "20%",
          containLabel: true,
        },
        xAxis: {
          type: "category",
          data: [
            "1月",
            "2月",
            "3月",
            "4月",
            "5月",
            "6月",
            "7月",
            "8月",
            "9月",
            "10月",
            "11月",
            "12月",
          ],
          axisLabel: {
            color: "#FFFFFF",
          },
          splitLine: {
            show: false,
          },
        },
        yAxis: {
          type: "value",
          axisLabel: {
            color: "#FFFFFF",
          },
          splitLine: {
            show: false,
          },
        },
        series: [
          {
            data: [
              820, 932, 901, 934, 1290, 1330, 1320, 820, 932, 901, 934, 1290,
            ],
            type: "line",
            symbol: "none",
            smooth: true,
            lineStyle: {
              color: {
                type: "linear",
                x: 0,
                y: 0,
                x2: 0,
                y2: 1,
                colorStops: [
                  {
                    offset: 0,
                    color: "#18E2FC",
                  },
                  {
                    offset: 0.125,
                    color: "#FF4EDC",
                  },
                  {
                    offset: 0.25,
                    color: "#14E0FB",
                  },
                  {
                    offset: 0.375,
                    color: "#12DDFA",
                  },
                  {
                    offset: 0.5,
                    color: "#FF3DCF",
                  },
                  {
                    offset: 0.625,
                    color: "#12D8F7",
                  },
                  {
                    offset: 0.75,
                    color: "#FF57D2",
                  },
                  {
                    offset: 0.875,
                    color: "#12DDFA",
                  },
                  {
                    offset: 1,
                    color: "#FF67D2",
                  },
                ],
                global: false,
              },
            },
          },
        ],
      });
    },
  },
};
</script>

<style lang="less" scoped>
.el-row {
  margin-bottom: 20px;
  &:last-child {
    margin-bottom: 0;
  }
}
.el-col {
  border-radius: 4px;
}
.grid-content {
  border-radius: 4px;
  min-height: 36px;
}
